:root {
    --transition-1: background-color 0.3s ease;
    --border-radius-1: 10px;
}

html, body {
    height: 100%;
    margin: 0;
}

html {
    background-color: black;
}

body {
    background: linear-gradient(rgba(0, 0, 0, 1), rgba(0, 0, 0, 0.9)), url('https://img.freepik.com/free-vector/flat-design-movie-theater-background_23-2150998489.jpg?semt=ais_incoming') fixed center;
    background-size: cover;
}

input[type="date"]::-webkit-datetime-edit {
    display: none;
}