.movie-repertoire {
    text-align: center;
}

#date-picker {
    font-size: 40px;
    border-radius: var(--border-radius-1);
    transition: var(--transition-1);
    border: 0;
}

#date-picker:hover {
    background-color: gray;
}

#movie-repertoire-h1 {
    color: white;
    padding-top: 20px;
}

#no-date-selected-info {
    border-radius: var(--border-radius-1);
    font-size: 15px;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    opacity: 0.35;
}

.movies-list {
    margin-top: 60px;
    padding-bottom: 60px;
}

#movies-list-item {
    border-radius: var(--border-radius-1);
    margin-top: 35px;
    background-color: rgba(169, 169, 169, 0.83);
    color: black;
    font-weight: 500;
    width: 60vw;
    margin-left: auto;
    margin-right: auto;
}